import {Formik, Form, Field, ErrorMessage} from 'formik';
import ToolTipe from "../../common/ToolTipe";
import styles from './LoginPage.module.css';

const validate = (values) => {
    const errors = {};
    if (!values.email) {
        errors.email = 'Required';
    } else if (
        !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
    ) {
        errors.email = 'Invalid email address';
    }
    return errors;
}
const initialValues = {email: '', password: '', rememberMe: false, captchaText: ''}
//инитал из стора!
const Login = ({loginThunk, captcha}) => (

    <Formik
        initialValues={initialValues}
        validate={validate}
        onSubmit={(loginData, {setSubmitting, resetForm, setStatus}) => {
            loginThunk(loginData, setStatus);
            setSubmitting(false);
            resetForm();

        }}
    >
        {({isSubmitting, status}) => (
            <Form>
                <Field type="email" name="email"/>
                <ErrorMessage name="email" component="div"/>
                <Field type="password" name="password" autoComplete="on"/>
                <ErrorMessage name="password" component="div"/>
                <div>
                    <label>
                        <Field type="checkbox" name="rememberMe"/>
                        Save user
                    </label>

                    <ToolTipe title="Save user Check this box
                               if you want your account information
                                saved for quick login on this device"
                    >
                        ?
                    </ToolTipe>

                </div>
                {captcha &&
                    <div>
                        <Field type="text" name="captchaText"/>
                        <img src={captcha} alt=""/>
                    </div>
                }
                <div>
                    {status}
                </div>
                <button type="submit" disabled={isSubmitting}>
                    Sign in
                </button>
            </Form>
        )}
    </Formik>

);

export default Login;